import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import moment from "moment";
import NumberFormat from "../../../App/NumberFormat";

import notification from "../../../../components/notification";
import { Empty, Collapse, Select, Row, Col, Divider, DatePicker } from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/report/balanceSheetComparativeReport/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, { Box, customCollapseStyle } from "../../../App/app.style";
import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import ExcelWrapper from "../../../App/ExcelWrapper";
import PDFWrapper from "../../../App/PDFWrapper";
import getLanguage from "../../../App/MyLanguange";
import { siteConfig } from "../../../../settings";
import { getFileName } from "../../../../helpers/helper"

const Panel = Collapse.Panel;
const Option = Select.Option;
// const { RangePicker } = DatePicker;

const redirectTo = "/dashboard/report-menus";
const xx = 15;


class BalanceSheetComparativeReport extends Component {
  state = {
    filter: {
      first_transaction_date: moment().startOf('month').subtract('seconds', 1).format("YYYY-MM-DD HH:mm:ss"),
      second_transaction_date: moment().format("YYYY-MM-DD HH:mm:ss"),
      id_location: null
    }
  };

  // Get All data diawal
  componentDidMount() {
    const { getBalanceSheetComparativeReports } = this.props;
    getBalanceSheetComparativeReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.balanceSheetComparativeReport.error) {
      notification("error", this.props.balanceSheetComparativeReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.balanceSheetComparativeReport.success) {
      notification("success", this.props.balanceSheetComparativeReport.success);
      this.props.clearSuccess();
    }
  }

  // Fungsi marakke mumet ndase 
  hitungTotalAmountParent = (array_ori, array_filter) => {
    //console.log(array_filter)
    array_filter.map((data) => {
      if (data.has_child === 1) {
        let filter = array_ori.filter((x) => x.id_account_parent === data.id_account)
        if (filter.length > 0) {
          this.hitungTotalAmountParent(array_ori, filter)
        }
      }
      else if (data.id_account_parent === null) {
        console.log("Ya")
      }
      else {

        let parent = array_ori.find((x) => x.id_account === data.id_account_parent)
        array_ori.map((x) => {

          if (x.id_account === parent.id_account) {
            let parent2 = parent
            while (parent2 !== undefined) {
              parent2.amount += parseFloat(data.amount)
              parent2 = array_ori.find((y) => y.id_account === parent2.id_account_parent)
              //console.log(parent2)
            }
          }
        })
      }
    })
  }

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    }
    else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    }
    else if (name === "first_transaction_date" || name === "second_transaction_date") {
      //console.log(value)
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value.format("YYYY-MM-DD HH:mm:ss")
        }
      });
    }
    else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.id_location) {
      string += "id_location=" + filter.id_location;
    }
    if (filter.first_transaction_date) {
      string
        ? (string +=
          "&first_transaction_date=" + filter.first_transaction_date)
        : (string +=
          "first_transaction_date=" + filter.first_transaction_date);
    }
    if (filter.second_transaction_date) {
      string
        ? (string +=
          "&second_transaction_date=" + filter.second_transaction_date)
        : (string +=
          "second_transaction_date=" + filter.second_transaction_date);
    }

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getBalanceSheetComparativeReports } = this.props;
    //const { current_page } = this.props.balanceSheetComparativeReport;
    let filter = this.filterValue();

    this.setState({ search: null });
    getBalanceSheetComparativeReports(1, null, filter, this.state.filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getBalanceSheetComparativeReports } = this.props;
    this.setState({
      filter: {
        first_transaction_date: moment().startOf('month').subtract('seconds', 1).format("YYYY-MM-DD HH:mm:ss"),
        second_transaction_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        id_location: null
      }
    });
    getBalanceSheetComparativeReports();
  };

  render() {
    // console.log(this.props.balanceSheetComparativeReport);
    // Data setup
    const {
      first_balance_sheet_reports,
      second_balance_sheet_reports,
      locations, loading, loadingForeign, filter, filter_select } =
      this.props.balanceSheetComparativeReport ? this.props.balanceSheetComparativeReport : [];

    // Definisi kolom2 serta field2 API yang dipakai tabel
    const columns = [
      {
        title: getLanguage({ id: "accountName" }),
        dataIndex: "account_name",
        rowKey: "account_name",
        width: "30%",
        render: (text, row) => {
          if (row.has_child) {
            if (row.level > 1) {
              return (
                <strong style={{ marginLeft: `${row.level * xx}px` }}>
                  {text}
                </strong>
              );
            } else {
              return <strong>{text}</strong>;
            }
          }
          else if (row.level === 1) {
            return text;
          }
          else {
            return (
              <span style={{ marginLeft: `${row.level * xx}px` }}>{text}</span>
            );
          }
        }
      },
      {
        title: `${getLanguage({ id: "previousAmount" })} (IDR)`,
        dataIndex: "previous_amount",
        rowKey: "previous_amount",
        align: "right",
        width: "15%",
        parse: true,
        render: (text, row) => {
          return row.has_child === 1
            ?
            !row.account_name.includes("Total") &&
              row.level === 1
              ? ""
              :
              (
                <strong>
                  <NumberFormat value={parseFloat(text)} />
                </strong>
              )
            :
            <NumberFormat value={parseFloat(text)} />

        }
      },
      {
        title: `${getLanguage({ id: "currentAmount" })} (IDR)`,
        dataIndex: "current_amount",
        rowKey: "current_amount",
        align: "right",
        width: "15%",
        parse: true,
        render: (text, row) => {
          return row.has_child === 1
            ?
            !row.account_name.includes("Total") &&
              row.level === 1
              ? ""
              :
              (
                <strong>
                  <NumberFormat value={parseFloat(text)} />
                </strong>
              )
            :
            <NumberFormat value={parseFloat(text)} />

        }
      },
      {
        title: `${getLanguage({ id: "variance" })} (IDR)`,
        dataIndex: "variance",
        rowKey: "variance",
        align: "right",
        width: "15%",
        parse: true,
        render: (text, row) => {
          return row.has_child === 1
            ?
            !row.account_name.includes("Total") &&
              row.level === 1
              ? ""
              :
              (
                <strong>
                  <NumberFormat value={parseFloat(text)} />
                </strong>
              )
            :
            <NumberFormat value={parseFloat(text)} />

        }
      },
      {
        title: getLanguage({ id: "%ofVar" }),
        dataIndex: "percentage",
        rowKey: "percentage",
        align: "right",
        width: "5%",
        parse: true,
        render: (text, row) => {
          return row.has_child === 1
            ?
            !row.account_name.includes("Total") &&
              row.level === 1
              ? ""
              :
              (
                <strong>
                  <NumberFormat value={parseFloat(text)} />
                </strong>
              )
            :
            <NumberFormat value={parseFloat(text)} />

        }
      },
    ]

    //console.log(first_balance_sheet_reports)
    let previous_balance_sheets = first_balance_sheet_reports
    let current_balance_sheets = second_balance_sheet_reports

    previous_balance_sheets.map((data, i) => {
      if (data.has_child === 1) {
        data.amount = 0
        current_balance_sheets[i].amount = 0
      }
      // else{
      //   data.amount = 1
      // }
    })

    if (previous_balance_sheets.length > 0) {
      let level_1 = previous_balance_sheets.filter((x) => x.level === 1)
      this.hitungTotalAmountParent(previous_balance_sheets, level_1);
    }

    if (current_balance_sheets.length > 0) {
      let level_1 = current_balance_sheets.filter((x) => x.level === 1)
      this.hitungTotalAmountParent(current_balance_sheets, level_1);
    }


    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    let total_asset = {
      account_name: "Total Asset",
      has_child: 1,
      level: 1,
      previous_amount: 0,
      current_amount: 0,
      variance: 0,
      percentage: 0,
    };
    let total_liabilities = {
      account_name: "Total Liabilities",
      has_child: 1,
      level: 1,
      previous_amount: 0,
      current_amount: 0,
      variance: 0,
      percentage: 0,
    };
    let total_equity = {
      account_name: "Total Equity",
      has_child: 1,
      level: 1,
      previous_amount: 0,
      current_amount: 0,
      variance: 0,
      percentage: 0,
    };
    let total_liabilities_equity = {
      account_name: "Total Liabilities & Equity",
      has_child: 1,
      level: 1,
      previous_amount: 0,
      current_amount: 0,
      variance: 0,
      percentage: 0,
    };
    let array_asset = [];
    let array_liabilities = [];
    let array_equity = [];

    //console.log(balance_sheets);
    previous_balance_sheets.map((data, i) => {
      if (data.gl_type === 1) {
        if (data.level === 1) {
          total_asset.previous_amount = data.amount
          total_asset.current_amount = current_balance_sheets[i].amount
        }

        let previous_amount = parseFloat(data.amount);
        let current_amount = parseFloat(current_balance_sheets[i].amount);
        let variance = current_amount - previous_amount;
        let percentage = previous_amount ? variance / previous_amount * 100 : 0

        array_asset.push({
          ...data,
          previous_amount,
          current_amount,
          variance,
          percentage
        });
      }
      else if (data.gl_type === 2) {
        if (data.level === 1) {
          total_liabilities.previous_amount = data.amount
          total_liabilities.current_amount = current_balance_sheets[i].amount
        }

        let previous_amount = parseFloat(data.amount);
        let current_amount = parseFloat(current_balance_sheets[i].amount);
        let variance = current_amount - previous_amount;
        let percentage = previous_amount ? variance / previous_amount * 100 : 0;

        array_liabilities.push({
          ...data,
          previous_amount,
          current_amount,
          variance,
          percentage
        });
      }
      else if (data.gl_type === 3) {
        if (data.level === 1) {
          total_equity.previous_amount = data.amount
          total_equity.current_amount = current_balance_sheets[i].amount
        }

        let previous_amount = parseFloat(data.amount);
        let current_amount = parseFloat(current_balance_sheets[i].amount);
        let variance = current_amount - previous_amount;
        let percentage = previous_amount ? variance / previous_amount * 100 : 0;

        array_equity.push({
          ...data,
          previous_amount,
          current_amount,
          variance,
          percentage
        });
      }
    })

    let dataSource = []

    if (previous_balance_sheets.length > 0) {
      total_liabilities_equity.previous_amount = total_equity.previous_amount + total_liabilities.previous_amount;
      total_liabilities_equity.current_amount = total_equity.current_amount + total_liabilities.current_amount;

      // hitung variance
      total_asset.variance = total_asset.current_amount - total_asset.previous_amount;
      total_equity.variance = total_equity.current_amount - total_equity.previous_amount;
      total_liabilities.variance = total_liabilities.current_amount - total_liabilities.previous_amount;
      total_liabilities_equity.variance = total_liabilities_equity.current_amount - total_liabilities_equity.previous_amount;

      // hitung percentage
      total_asset.percentage = total_asset.previous_amount
        ? total_asset.variance / total_asset.previous_amount * 100
        : 0;
      total_equity.percentage = total_equity.previous_amount
        ? total_equity.variance / total_equity.previous_amount * 100
        : 0;
      total_liabilities.percentage = total_liabilities.previous_amount
        ? total_liabilities.variance / total_liabilities.previous_amount * 100
        : 0;
      total_liabilities_equity.percentage = total_liabilities_equity.previous_amount
        ? total_liabilities_equity.variance / total_liabilities_equity.previous_amount * 100
        : 0;

    }

    if (previous_balance_sheets.length > 0) {

      // Sing total Asset 
      array_asset.map((data) => {
        dataSource.push({
          ...data,
          // percentage: total_asset.previous_amount ? data.variance/total_asset.previous_amount*100 : 0 
        })
      })

      dataSource.push({
        ...total_asset,
        // percentage: total_asset.amount ? total_asset.amount/total_asset.amount*100 : 0 
      });

      // Sing liability equity 
      // data liability
      array_liabilities.map((data) => {
        dataSource.push({
          ...data,
          //percentage: total_liabilities_equity.amount ? data.amount/total_liabilities_equity.amount*100 : 0 
        })
      })
      // total liability
      dataSource.push({
        ...total_liabilities,
        //percentage: total_liabilities_equity.amount ? total_liabilities.amount/total_liabilities_equity.amount*100 : 0 
      });

      // data equity

      array_equity.map((data) => {
        dataSource.push({
          ...data,
          // percentage: total_liabilities_equity.amount ? data.amount/total_liabilities_equity.amount*100 : 0 
        })
      })

      // total equity

      dataSource.push({
        ...total_equity,
        // percentage: total_liabilities_equity.amount ? total_equity.amount/total_liabilities_equity.amount*100 : 0 
      });

      // total liability equity

      dataSource.push({
        ...total_liabilities_equity,
        // percentage: total_liabilities_equity.amount ? total_liabilities_equity.amount/total_liabilities_equity.amount*100 : 0 
      });
    }


   // console.log(dataSource)

    let dataExcel = [];
    dataSource.map((data, index) => {
      data.key = index
      dataExcel.push({
        ...data,
        account_name: (data.level > 1 ? ' '.repeat(data.level * 3) + data.account_name : data.account_name)
      })
    })
    // console.log(total_asset)
    const fileName = getLanguage({ id: "balanceSheetComparativeReports.pageHeader" })

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="balanceSheetComparativeReports.pageHeader" />}
            marginBottom={true}
          >
            <PrintWrapper
              componentRef={ref => (this.componentRef = ref)}
              title={<IntlMessages id="balanceSheetComparativeReports.pageHeader" />}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
            //height={"140px"}
            // filterDate="first_transaction_date"
            // dateFormat="DD MMMM YYYY HH:mm:ss"
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
              onBeforePrint={() => (document.title = getFileName(fileName, null, false))}
              onAfterPrint={() => document.title = siteConfig.documentTitle}
              pageStyle={
                "@page { size: landscape; }" +
                "body {" +
                  "-webkit-print-color-adjust: exact;" +
                "} "
              }
            />

            <PDFWrapper
              columns={columns}
              data={dataSource}
              fileName={fileName}
              filter={filter_select}
              loading={loading}
              locations={locations}
              scale={0.4}
              title="balanceSheetComparativeReports.pageHeader"
            />

            <ExcelWrapper
              title="balanceSheetComparativeReports.pageHeader"
              fileName={fileName}
              loading={loading}
              columns={columns}
              data={dataExcel}
              filter={filter_select}
              locations={locations}
            // filterDate="first_transaction_date"
            // dateFormat="DD MMMM YYYY HH:mm:ss"
            />

            <Button style={{ marginLeft: "5px" }} onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "24px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="select.location" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                    //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                  <Col span={12}>
                    <Col span={12}>
                      <label>
                        <IntlMessages id="previousDate" />
                      </label>
                      <br />
                      <DatePicker
                        style={{ width: "95%" }}
                        showTime
                        format="DD MMMM YYYY HH:mm:ss"
                        onChange={this.handleFilterChange.bind(
                          this,
                          "first_transaction_date"
                        )}
                        value={
                          this.state.filter.first_transaction_date
                            ? moment(this.state.filter.first_transaction_date)
                            : moment()
                        }
                        allowClear={false}
                        disabledDate={e => e.valueOf() > moment(this.state.filter.first_transaction_date)}
                      />
                    </Col>
                    <Col span={12}>
                      <label>
                        <IntlMessages id="currentDate" />
                      </label>
                      <br />
                      <DatePicker
                        style={{ width: "100%" }}
                        showTime
                        format="DD MMMM YYYY HH:mm:ss"
                        onChange={this.handleFilterChange.bind(
                          this,
                          "second_transaction_date"
                        )}
                        value={
                          this.state.filter.second_transaction_date
                            ? moment(this.state.filter.second_transaction_date)
                            : moment()
                        }
                        allowClear={false}
                        disabledDate={e => e.valueOf() < moment(this.state.filter.first_transaction_date)}
                      />
                    </Col>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <Col span={12}
                    // style={{ marginTop: "22px" }}
                    >
                      <Button
                        size="small"
                        type="primary"
                        style={{ marginRight: "10px" }}
                        onClick={this.handleFilter}
                      >
                        <IntlMessages id="filter.only" />
                      </Button>
                      <Button onClick={this.handleResetFilter} size="small">
                        <IntlMessages id="filter.reset" />
                      </Button>
                    </Col>
                  </Col>
                </Row>

              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {
              !filter
                ? (<Empty description={<IntlMessages id="clickFilter" />} />)
                :
                previous_balance_sheets.length === 0 &&
                  !this.props.balanceSheetComparativeReport.loading ? (
                    <Empty
                      description={
                        <IntlMessages id="balanceSheetComparativeReports.table.noData" />
                      }
                    />
                  ) : (
                    <div className="isoInvoiceTable">
                      <Divider orientation="left" style={{ fontSize: 14 }}>
                        <IntlMessages id="details" />
                      </Divider>
                      <Scrollbars style={{ width: "100%" }}>
                        <TableWrapper
                          dataSource={dataSource}
                          rowKey={record => record.key}
                          columns={columns}
                          loading={loading}
                          className="invoiceListTable"
                          bordered={true}
                          // Set pagination, jika ada page_size mengunakan pagination API
                          // jika tidak ada page_size menggunakan pagination bawaan
                          pagination={false}
                        />
                      </Scrollbars>
                    </div>
                  )
            }
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    balanceSheetComparativeReport: state.balanceSheetComparativeReport,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(BalanceSheetComparativeReport);
